import React, {useContext} from 'react'
import { NavigateContext } from '../../context/Navigate'

export default function PlaceServices() {
    const {handleNavigate} = useContext(NavigateContext)

  return (
    <section className='placeServices__container'>
        <div className='placeServices'>
            <div className="placeServices__content">
                <h3 className="placeServices__title">   <br />    Good Day</h3>
                <p className="placeServices__text">Good Day    <br /> -,   <br /> </p>
                <button onClick={() => handleNavigate('aboutapp', 'aboutapp')} className="placeServices__button">  Good Day</button>
            </div>
            <img src="" alt="" className="placeServices__img" />
        </div>
    </section>
)
}
